/*HEADER AND LEGEND WRAP*/
.cardTitleWrap{
    display: inline-block;
    width: 100%;
    padding-bottom: 20px;
}

    /*HEADER*/
.headerWrap{
    display: inline-block;
}

    /*LEGEND*/
.legend{
    width: auto;
    display: -ms-inline-flexbox;
    display: inline-flex;
    position: relative;
    float: right;
}

.colorsWrap{
    width: 99px;
    height: 20px;
    margin-left: 5px;
    margin-right: 5px;
    display: -ms-inline-flexbox;
    display: inline-flex
}

.lightLegendColor, .mediumLegendColor, .darkLegendColor{
    width: 33px;
    height: 15px;
    display: inline-block;
}

.colorIndicator{
    margin: 0;
    color: hsl(0, 0%, 45%);
    font-size: 12px;
}

/*OVERLAP CALENDAR CONTENT*/
.availabilityCalendar{
    position: static;
}

.overlapDays, .daysSimple, .timesRow, .timeBlock{
    display: inline-block;
    width: 100%
}

.colorIndicator{
    display: inline-block;
}

    /*COLUMN WITH BLOCKS FOR EACH DAY MON-SUN*/
.overlapDays{
    width: 12.5%;
    margin-top: 8px;  
}

    /*AM-PM TIMES ON LEFT SIDE*/
.timesRow{
    margin-right: 15px;
    vertical-align: top;
}
    /*INDIVIDUAL TIME ON LEFT SIDE*/
.timesSimple{
    height: 25px;
    font-size: 12px;
    width: 100%;
    text-align: center;
    -o-text-overflow: clip;
       text-overflow: clip; 
    /* border-bottom: 0.5px solid rgb(219, 219, 219); */
}

    /*INDIVIDUAL DAYS MON-SUN ON TOP*/
.daysSimple {
    width: 12.5%;
    height: 25px;
    font-size: 12px;
    text-align: center;
}

    /*AVAILABILITY BLOCKS*/
.overlapTimes {
    height: 25px;
    width: 100%;
    border: 1px solid rgb(219, 219, 219);
}



